module.exports={posts:[{"id":1,"title":"What is Git!","username":"Sheik Mohammed Adil","body":"<p>Git is a version-control system (VCS) for tracking changes in computer files and coordinating work on those files among multiple people. It is primarily used for source-code management in software development, but it can be used to keep track of changes in any set of files. As a distributed revision-control system, it is aimed at speed, data integrity, and support for distributed, non-linear workflows. There are multiple Version Control Systems, but Git is by far and large the most popularboth for individual and company use.</p>"+"<p>Git was created by Linus Torvalds in 2005 for development of the Linux kernel, with other kernel developers contributing to its initial development.</p>"+"<p>Version control is the only reasonable way to keep track of changes in code, manuscripts, presentations, and data analysis projects. I used to make numbered tar.gz files for a project. But exploring the differences is difficult, to say the least. And if you use git properly, youll have annotated each small change. Unless you can write something perfectly the first time, you need to have a version control system .</p>"+"<h3>Git repositories</h3>"+"<p>A Git repository contains the history of a collection of files starting from a certain directory. The process of copying an existing Git repository via the Git tooling is called cloning. After cloning a repository the user has the complete repository with its history on his/her local machine. Of course, Git also supports the creation of new repositories.</p>"+"<h3>The concept of branches</h3>"+"<p>Git supports branching which means that you can work on different versions of your collection of files. A branch allows the user to switch between these versions so that he can work on different changes independently from each other.</p>"+"<p>For example, if you want to develop a new feature, you can create a branch and make the changes in this branch. This does not affect the state of your files in other branches. For example, you can work independently on a branch called production for bugfixes and on another branch called new_feature for implementing a new feature.</p>"+"<p>Ok, I think it's more than enough for you to understand What <b>Git</b> is. Heres an awesome (free!) interactive tutorial sponsored by GitHub where you can learn all of the basics: <a class='link' href=\"https://try.github.io/\" target=\"blank\">try.github.io</a></p>"+"<p>Also participate in our event on Git & GitHub 101 to know more about Git and it's uses . Check out the details <a class='link' href=\"https://elmentrix.github.io/glugpace/events/1/\">here</a></p>","imageURl":"./post-images/what-is-git/git-post.jpeg"},{"id":2,"title":"My experience of Techyon","username":"Waseema Imtiaz Shaikh","body":"<p>The event Techyon was held on the 15th of February, in exchange to our extra-long-seeming lecture hours of Friday mornings.</p>"+"<p>It started off by Arkaan Sayed who gave a brief introduction about what to expect from this event.</p>"+"<p>The first session dealt with the basic introduction to Cloud Computing and what are its uses, which was done by Muhammed Shakir and Ahmed Ansad.</p>"+"<img src='https://raw.githubusercontent.com/mohammed-adil/glugpace/master/src/components/blog/post-images/my-experience-of-techyon/01.jpg' alt='--=--'>"+"<p>Ahmed Ansad demonstrated the hosting of a searX.me instance from local machine.</p>"+"<p>To make the event more interactive and to lighten the mood of the audience, a dare game was held, where a number was called at random and the person sitting on the chair corresponding to that number had to come forward and do a dare.</p>"+"<img src='https://raw.githubusercontent.com/mohammed-adil/glugpace/master/src/components/blog/post-images/my-experience-of-techyon/02.jpg' alt='--=--'>"+"<p>Moving on, the next session was taken up by Vishnu Bhagirathan, Sharon M and Shayiz KP.</p>"+"<img src='https://raw.githubusercontent.com/mohammed-adil/glugpace/master/src/components/blog/post-images/my-experience-of-techyon/03.jpg' alt='--=--'>"+"<p>Vishnu gave an introduction to Artificial Intelligence, followed by Sharon who gave a gist about the simplicity of the Python programming language. Next up, was Shayiz. He spoke about the importance of doing something worthwhile in life by gaining knowledge, and how Glug can help with that through the conduction of various workshops and more. This really helped to build up the interest of the audience in Glug and to contribute more to it.</p>"+"<p>The next session was about IoT- Internet of Things and where it is applied in our daily life, including a brief about the Micro Bit. This was by Munawir E and Sabith OK.</p>"+"<img src='https://raw.githubusercontent.com/mohammed-adil/glugpace/master/src/components/blog/post-images/my-experience-of-techyon/04.jpg' alt='--=--'>"+"<p>The speakers had also attended the FSMK camp during the summer break and were very keen on sharing their experiences in order to encourage the students to attend the camp the next time it's held. They spoke about how the camp had technical and non-technical events, and helped in meeting new people and discussing ideas, and kept stressing on how enjoyable it was on a whole.</p>"+"<p>Altogether, the event was a success as it managed to gather the interest of the 2nd year students and inspire them to look outside the curriculum.</p>"+"<img src='https://raw.githubusercontent.com/mohammed-adil/glugpace/master/src/components/blog/post-images/my-experience-of-techyon/05.jpg' alt='--=--'>"+"<p>Being a member of GEM was a new experience for us and helped us learn about the work that needs to be done in the management of an event. It gave us an opportunity to display our ideas and talents by designing posters, organising the basic layout and giving our suggestions on how to improve the event.</p>","imageURl":"./post-images/my-experience-of-techyon/techyon.jpg"},{"id":3,"title":"FSMK Winter Camp-2k19","username":"Sheik Mohammed Adil","body":"<p>The 5-day residential camp which was held from 20-24 Jan 2019 at PESCE, Mandya concluded successfully.</p>"+"<p>FSMK camps present a different perspective to technology. As much as the people in FSMK love technology, they also care deeply about our society and the impact of technology in our society. FSMK camps not only focus on imparting technology, but also has various talks about a technology focussed society and how every individual can contribute towards a safer and ethical techno-society. </p>"+"<p>FSMK camps are residential in nature, where we take care of stay, food and other things during the days of the camp. The camp offered 3 different technical tracks .</p>"+"<ul>"+"<li>Data Science & Artificial Intelligence</li>"+"<li>DevOps & Cloud Computing</li>"+"<li>Open Hardware & Internet of Things</li>"+"</ul>"+"<img src='https://raw.githubusercontent.com/mohammed-adil/glugpace/master/src/components/blog/post-images/fsmk-winter-camp-2k19/tracks.png' alt='--=--'>"+"<p>Participants at FSMK Camp had surprise awaiting for them, when the FSMK released a official statement regarding a guest talk on A Free Digital Society by none other than Dr. Stallman (aka RMS).</p>"+"<img src='https://raw.githubusercontent.com/mohammed-adil/glugpace/master/src/components/blog/post-images/fsmk-winter-camp-2k19/stallman.png' alt='--=--'>"+"<p>The camp had around 111 participants and 40 speakers/volunteers from around 6-7 colleges spread across districts of Karnataka. Apart from the topics that the track will teach, participants also had various sessions on Free(dom) software, Open hardware, Privacy, Digital economy.</p>"+"<p>As for me I was volunteering for Data Science & AI track, which made me understand the concepts of Data science & AI along with which I got to meet new people and learn their perspective of technology and its use.</p>"+"<p>One thing about the camp was that throughout the camp both AI and Cloud tracks were fighting amongst each other in becoming more awesome track (actually it was amongst speakers and volunteers) and we rarely heard anything from IOT track.</p>"+"<br>"+"<img src='https://raw.githubusercontent.com/mohammed-adil/glugpace/master/src/components/blog/post-images/fsmk-winter-camp-2k19/ai-track.jpeg' alt='--=--'>"+"<h4 style='text-align:center'>Data Science & AI Track</h4>"+"<img src='https://raw.githubusercontent.com/mohammed-adil/glugpace/master/src/components/blog/post-images/fsmk-winter-camp-2k19/cloud-track.jpg' alt='--=--'>"+"<h4 style='text-align:center'>DevOps & Cloud Computing Track</h4>"+"<img src='https://raw.githubusercontent.com/mohammed-adil/glugpace/master/src/components/blog/post-images/fsmk-winter-camp-2k19/iot-track.jpg' alt='--=--'>"+"<h4 style='text-align:center'>Internet of Things Track</h4>"+"<p>Apart from technical sessions, we also had non-tech sessions that was taken by our FSMK speakers. Each day speakers used to take non-tech sessions where they talked about a technology focussed society and how every individual can contribute towards a safer and ethical techno-society. And participants used to interact/ask freely with the speakers about their thoughts regarding the topics.</p>"+"<img src='https://raw.githubusercontent.com/mohammed-adil/glugpace/master/src/components/blog/post-images/fsmk-winter-camp-2k19/non-tech.jpg' alt='--=--'>"+"<p>Non tech sessions were also taken during evening at around 7pm, along with which we used play some activity (games, dance and singing) where all of us had lot of fun.</p>"+"<p>Ok, I think it's more than enough for you to understand how FSMK camp was for me. If you've missed it this time there's always next time for you!. Wait patiently for it and you won't regret it a bit.</p>"+"<img src='https://raw.githubusercontent.com/mohammed-adil/glugpace/master/src/components/blog/post-images/fsmk-winter-camp-2k19/fsmk-group.jpeg' alt='--=--'>","imageURl":"./post-images/fsmk-winter-camp-2k19/fsmk-camp-post.jpeg"}]};

